.profile__avatar-box {
    width: 120px;
    height: 120px;
    border-radius: 50%;
    overflow: hidden;
    position: relative;
    padding: 0;
    margin: 0;
    border: none;
    cursor: pointer;
}

.profile__avatar-box::before {
    opacity: 0;
    content: "";
    position: absolute;
    width: 100%;
    height: 100%;
    background-color: #000000cc;
    background-image: url("../../../images/edit-avatar-icon.svg");
    background-repeat: no-repeat;
    background-position: center;
    background-size: 26px 26px;
    border-radius: 50%;
}

.profile__avatar-box:hover::before {
    opacity: 1;
    transition: all 0.3s;
}

@media screen and (max-width: 700px) {
    .profile__avatar-box {
      margin: 0 auto;
      }
  } 