.profile__name {
    font-style: normal;
    font-weight: 500;
    font-size: 42px;
    line-height: 48px;
    color: #FFFFFF;
    margin: 0;
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
    max-width: 294px;
}

@media screen and (max-width: 500px) {
    .profile__name {        
        font-size: 27px;
        line-height: 33px;
        text-align: center;
        max-width: 282px;
      }
  } 